.app {
  text-align: center;
  background-color: #000000;
  overflow: hidden;
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: #2b2b2b;
}

::-webkit-scrollbar-thumb {
  background: #575757;
}

::-webkit-scrollbar-thumb:hover {
  background: #c99701b9;
}

.flex {
  display: flex;
}