/* -------- */
/*  Static  */

.app-header {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.app-container {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
}

.landing-container {
    flex-direction: column;
}


/* --------- */
/* Animation */

.app-container {
    animation: fadeInAnimation ease-out 0.5s;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
}

@keyframes fadeInAnimation {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}