.nav-buttons {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    height: 100%;
    padding: 0;
    margin: 0;
    list-style: none;
}

.nav-button {
    color: #fff;
    text-decoration: none;
}

.nav-button:hover {
    color: #d9ae2d;
}

.clickable:hover {
    cursor: pointer;
    text-shadow: 0 0 1.7rem #ffc107;
}

.clickable:active {
    cursor: pointer;
    text-shadow: 0 0 1.7rem #ffc107;
    transform: translateY(2px);
}