.fadeIn {
    animation: 0.25s fadeIn forwards;
}

.fadeOut {
    animation: 0.25s fadeOut forwards;
}

@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translate(0px, -25px);
    }

    to {
        opacity: 1;
        transform: translate(0px, 0px);
    }
}

@keyframes fadeOut {
    from {
        opacity: 1;
        transform: translate(0px, 0px);
    }

    to {
        transform: translate(0px, -35px);
        opacity: 0;
    }
}

.a-text-like {
    color: #fff;
}

.a-text-like:hover {
    color: #fff;
}
