.feature-preview {
    /* margin-right: 2rem; */
    margin-bottom: 2rem;
}

.feature-preview-image {
    width: 30rem;
    height: 30rem;
    object-fit: contain;
}

.feature-preview-image {
    width: 100%;
    position: relative;
}


.feature-preview {
    animation: 0.3s in forwards;

    /* Create an orange border */
    
}

.feature-preview:hover {
    animation: 0.3s out forwards;
}

@keyframes in {
    from {
        transform: translate(0px, 0px) scale(1.0);
    }

    to {
        transform: translate(0px, 0px) scale(0.9);
    }
}

@keyframes out {
    from {
        transform: translate(0px, 0px) scale(0.9);
    }

    to {
        transform: translate(0px, 0px) scale(1.0);
    }
}